// ==========
// Global Config
// =============

// 
// https://fonts.googleapis.com/css?family=Playfair+Display:400,700
// 'https://fonts.googleapis.com/css?family=Archivo+Black:400,700'
//https://fonts.googleapis.com/css?family=Frank+Ruhl+Libre:400,700

// font stacks 
$font-url--google        : 'https://fonts.googleapis.com/css?family=Roboto:400,700|Archivo+Black:400,700|Tenor+Sans:400';
$font-family--primary    : 'Roboto', Helvetica, Arial, sans-serif;
$font-family--secondary  : 'Archivo Black', Helvetica, Arial, sans-serif;
$font-family--tertiary  : 'Tenor Sans', Helvetica, Arial, sans-serif;

// font weights 
$font-weight--thin       : 100;
$font-weight--light      : 300;
$font-weight--medium     : 400;
$font-weight--bold       : 700;
$font-weight--ultra-bold : 900;


// Colors ===========================================
$white			: #fff;
$black			: #000;
$black-transparent: rgba(0,0,0,0.5);
$dark-transparent: rgba(0,0,0,0.85);
$grey-transparent: rgba(0,0,0,0.25);
$clean-transparent: rgba(255,255,255,0);

// greys 
$wild-sand		: #f5f5f5;
$silver 		: #bbb;
$finlandia		: #5b5c5b;
$boulder		: #777;
$emperor		: #555;
$mine-shaft		: #333;

// orange 
$sunglow		: #ffb732;


// Color usage =====================================

// greys
$grey--primary	: $boulder;
$grey--accent	: #4d4d4d;
$grey--dark		: #444;
$grey--light	: #898989;

// oranges
$orange--active	: $sunglow;
$orange--dark	: darken($orange--active, 5%);
$orange--light	: lighten($orange--active, 5%);

// text colours
$text-color-main: $finlandia;
$text-color-light: lighten($finlandia, 20%);

// breakpoints 
$bp-xs: 1;
$bp-sm: 576;
$bp-md: 768;
$bp-lg: 992;
$bp-xl: 1200;
$bp-xxl: 1400;

// map the breakpoints
$breakpoints: (
	'xs-min': $bp-xs,
	'sm-min': $bp-sm,
	'md-min': $bp-md,
	'lg-min': $bp-lg,
	'xl-min': $bp-xl,
	'xxl-min': $bp-xxl,
	'xs-max': $bp-sm - 1,
	'sm-max': $bp-md - 1,
	'md-max': $bp-lg - 1,
	'lg-max': $bp-xl - 1,
	'xl-max': $bp-xxl - 1,
);

// container max widths
$container-max-width-sm: 540px;
$container-max-width-ms: 720px;
$container-max-width-lg: 960px;
$container-max-width-xl: 1140px;

// spacing
$padding: 50;	

// paths to static assets
$static-img-path: '../../static/img';

// font sizes for body text
$font-sizes-body: (
	md: 18,
	lg: 22,
);

$font-sizes-h1: (
	md: 30,
	lg: 35,
);

// transition duration
$transition-duration: 0.25s;
$regions-slide-duration: 1s;


// TODO tidy these up:
// Nav 
$nav-height-sm		: 40;
$nav-height-lg		: 75;
$nav-border-radius	: 10;

// Border
$border--white	: dashed 1px $white;

// MISC
$list-itm-bg	: $wild-sand;

$demo-itm-bg	: $mine-shaft;
$demo-itm-bxs	: $silver;

$vertical-nav-width: 100px;


// Border radius
$br--default    : .4em;
$br--round      : 50%;

// Transitions
$trns-duration  : .4s;
$main-layout-transition: 1s;


// Path to Assets
$path--rel      : "../img";

// z indexes
$navigation-z-index: 10;
$region-z-index: 5;


