@import url("https://fonts.googleapis.com/css?family=Roboto:400,700|Archivo+Black:400,700|Tenor+Sans:400");
.row {
  display: flex;
  flex-wrap: wrap;
}

.row--center {
  justify-content: center;
}

.column {
  padding-left: 5%;
  padding-right: 5%;
}

@media (min-width: 48rem) {
  .column {
    padding-left: 0;
    padding-right: 0;
  }
}

.column-xs-12 {
  flex-basis: 100%;
}

@media (min-width: 48rem) {
  .column-m-10 {
    flex-basis: 83.33333%;
  }
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.padding {
  padding: 1.5625rem 0;
}

@media (min-width: 62rem) {
  .padding {
    padding: 3.125rem 0;
  }
}

.padding--ends {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

@media (min-width: 62rem) {
  .padding--ends {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }
}

.list--plain {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button--clear {
  outline: none;
  border: none;
  background: none;
}

.round {
  border-radius: 50%;
}

circle, .fill, .stroke, .highlightfill {
  transition: all 0.25s;
}

.font--primary {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
}

.center-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes fade-out-in {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes orangeblink {
  50% {
    border-color: #ffc04c;
  }
}

@keyframes roll {
  10% {
    transform: rotate(2.5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes shake-clockwise {
  80% {
    transform: rotate(0deg);
  }
  85% {
    transform: rotate(5deg);
  }
  90% {
    transform: rotate(0deg);
  }
  95% {
    transform: rotate(5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes pulse {
  80% {
    transform: scale(1);
  }
  85% {
    transform: scale(1.025);
  }
  90% {
    transform: scale(1);
  }
  95% {
    transform: scale(1.025);
  }
  100% {
    transform: scale(1);
  }
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}

html::-webkit-scrollbar,
body::-webkit-scrollbar {
  width: 0px;
}

body {
  background-color: #000;
  text-align: center;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  color: #5b5c5b;
}

#application {
  width: 100%;
  height: 100%;
}

#application > div {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child {
  margin-top: 0;
}

h1 {
  font-family: "Archivo Black", Helvetica, Arial, sans-serif;
}

h4 {
  font-size: 1.035em;
}

svg {
  overflow: visible;
  max-width: 100%;
  max-height: 100%;
}

.container {
  margin: 0 auto;
  width: 90%;
  max-width: 62.5rem;
}

@media (min-width: 48rem) {
  .container {
    margin: 0 auto;
    width: 80%;
  }
}

.regions {
  width: 100%;
  height: 100%;
  position: relative;
}

.primaryregion {
  width: 100%;
  height: 100%;
}

.region {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.region__icon {
  display: block;
  margin: 0 auto;
  width: 5rem;
}

@media (min-width: 62rem) {
  .region__icon {
    width: 9.375rem;
  }
}

.region__icon .stroke {
  stroke: #cccccc;
}

.region__icon .fill {
  fill: #cccccc;
}

.region__icon .iconbit {
  transform-origin: 50% 50%;
}

.region__icon .iconbit--screwdriver {
  fill: gray;
  animation: roll 3s 2.4s infinite;
}

.region__icon .iconbit--spanner {
  animation: shake-clockwise 3s infinite;
}

.region__icon .iconbit--at {
  animation: pulse 3s infinite;
}

.region__inner {
  min-height: 100%;
}

.region__header {
  padding: 0 1.875rem 1.5625rem 1.875rem;
}

@media (min-width: 62rem) {
  .region__header {
    padding: 0 3.125rem 2.5rem 3.125rem;
  }
}

.region__title {
  margin-bottom: 0;
}

.region__intro {
  margin: 1.25rem auto 0;
  max-width: 56.25rem;
}

@media (min-width: 62rem) {
  .region__intro {
    margin-top: 2.5rem;
  }
}

.module {
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
}

@media (min-width: 62rem) {
  .module {
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
  }
}

.direction--up, .direction--down {
  left: 50%;
  width: 4.375rem;
  height: 2.5rem;
  transform: translateX(-50%);
}

@media (min-width: 62rem) {
  .direction--up, .direction--down {
    width: 6.25rem;
    height: 3.125rem;
  }
}

.direction--up .direction__text, .direction--down .direction__text {
  left: -2.1875rem;
  width: 6.25rem;
  text-align: center;
}

.direction--up:hover i:first-of-type, .direction--down:hover i:first-of-type {
  transform: translate(10px, -10px) rotate(45deg);
}

.direction--up:hover i:last-of-type, .direction--down:hover i:last-of-type {
  transform: translate(-10px, 10px) rotate(45deg);
}

.direction--prev, .direction--next {
  top: 50%;
  transform: translateY(-50%);
}

.direction--prev:hover i:first-of-type, .direction--next:hover i:first-of-type {
  transform: rotate(135deg);
}

.direction--prev:hover i:last-of-type, .direction--next:hover i:last-of-type {
  transform: rotate(-45deg);
}

.directions--background .direction {
  background-color: rgba(0, 0, 0, 0.5);
}

.direction {
  display: block;
  position: fixed;
  z-index: 10;
  outline: none;
  color: #fff;
}

.direction:hover .direction__text {
  opacity: 1;
}

@media (min-width: 62rem) {
  .direction:hover .direction__icon::after {
    animation: none;
    opacity: 0;
  }
}

.direction--prev {
  left: 0.625rem;
}

@media (min-width: 62rem) {
  .direction--prev {
    left: 1.25rem;
  }
}

.direction--prev .direction__text {
  left: 0;
}

@media (min-width: 62rem) {
  .direction--prev .direction__icon {
    transform: rotate(-45deg);
  }
}

@media (max-width: 61.9375rem) {
  .direction--prev .direction__icon::after {
    transform: rotate(-45deg);
  }
}

.direction--next {
  right: 0.625rem;
}

@media (min-width: 62rem) {
  .direction--next {
    right: 1.25rem;
  }
}

.direction--next .direction__text {
  right: 0;
}

@media (min-width: 62rem) {
  .direction--next .direction__icon {
    transform: rotate(135deg);
  }
}

@media (max-width: 61.9375rem) {
  .direction--next .direction__icon::after {
    transform: rotate(135deg);
  }
}

.direction--up {
  top: 0;
  border-radius: 0 0 0.625rem 0.625rem;
}

.direction--up .direction__inner {
  margin: 0.3125rem auto 0;
}

@media (min-width: 62rem) {
  .direction--up .direction__inner {
    margin-top: 1.25rem;
  }
}

@media (min-width: 62rem) {
  .direction--up .direction__icon {
    transform: rotate(45deg);
  }
}

@media (max-width: 61.9375rem) {
  .direction--up .direction__icon::after {
    transform: rotate(45deg);
  }
}

.direction--down {
  bottom: 0;
  border-radius: 0.625rem 0.625rem 0 0;
}

@media (min-width: 62rem) {
  .direction--down .direction__icon {
    transform: rotate(225deg);
  }
}

@media (max-width: 61.9375rem) {
  .direction--down .direction__icon::after {
    margin-bottom: 0.3125rem;
    transform: rotate(225deg);
  }
}

.direction__inner {
  display: block;
  position: relative;
  margin: 0 auto;
  width: 1.25rem;
  height: 100%;
}

@media (min-width: 62rem) {
  .direction__inner {
    width: 1.875rem;
    height: 1.875rem;
  }
}

.direction__text {
  position: absolute;
  top: 0;
  line-height: 1.875rem;
  opacity: 0;
  transition: opacity 0.25s;
}

.direction__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 61.9375rem) {
  .direction__icon {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.direction__icon::after {
  content: "";
  display: block;
  width: 1.125rem;
  height: 1.125rem;
  background-image: url(/static/arrow.svg);
  animation: fade-in-out 1s infinite;
  transition: opacity 0.25s;
}

@media (max-width: 47.9375rem) {
  .direction__icon::after {
    background-size: 2rem;
  }
}

@media (min-width: 62rem) {
  .direction__icon::after {
    position: absolute;
    top: 0.53125rem;
    left: 0.53125rem;
    width: 0.78125rem;
    height: 0.78125rem;
  }
}

.direction i {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(/static/arrow-line.svg) no-repeat -0.03125rem;
  background-size: 160%;
  transition: transform 0.25s;
}

@media (max-width: 61.9375rem) {
  .direction i {
    display: none;
  }
}

@media (min-width: 48rem) {
  .direction i {
    background-size: cover;
  }
}

.direction i:first-of-type {
  transform: rotate(90deg);
}

.nav {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10;
  height: 2.5rem;
  min-width: 8.125rem;
  background-color: rgba(255, 255, 255, 0);
  transform: none;
  transition: transform 1s;
}

@media (min-width: 62rem) {
  .nav {
    height: 4.6875rem;
    padding: 0;
  }
}

.nav a {
  transition: color 0.25s;
  color: #fff;
}

.nav button {
  transition: color 0.25s;
}

.nav__menu {
  display: flex;
  border-radius: 0 0.625rem 0 0;
  width: 100%;
  height: 100%;
  padding: 0 0.3125rem;
  transition: background-color 0.25s;
}

.nav__item {
  display: flex;
  flex: 1 0 25%;
  order: 1;
  justify-content: center;
  align-items: center;
  transition: all 1s;
}

@media (min-width: 62rem) {
  .nav__item {
    width: 4.375rem;
    margin: 0;
  }
}

@media (min-width: 87.5rem) {
  .nav__item {
    width: 5.3125rem;
    margin: 0;
  }
}

.nav__item--logo {
  order: 0;
}

.nav__item--logo .nav__linkcircle {
  padding: 0.0625rem;
}

@media (min-width: 62rem) {
  .nav__item--logo .nav__linkcircle {
    border: none;
  }
}

.nav__item--logo .nav__icon {
  width: 100%;
  height: 100%;
}

.nav__item--logo .nav__linktext {
  display: none;
}

.nav__link {
  display: block;
}

@media (min-width: 62rem) {
  .nav__link {
    position: relative;
    margin-bottom: 0.3125rem;
  }
}

@media (min-width: 62rem) {
  .nav__link:hover .nav__linktext {
    transform: translate(-50%, 0.3125rem);
    opacity: 1;
  }
}

@media (max-width: 61.9375rem) {
  .nav__link--current .nav__linkcircle {
    background-color: #ffb732;
  }
}

.nav__linkcircle {
  display: flex;
  margin: 0 auto;
  border: 0.125rem solid #ffb732;
  border-radius: 50%;
  width: 0.9375rem;
  height: 0.9375rem;
  justify-content: center;
  align-items: center;
  transition: all 0.25s;
}

@media (min-width: 62rem) {
  .nav__linkcircle {
    width: 2.8125rem;
    height: 2.8125rem;
  }
}

@media (min-width: 87.5rem) {
  .nav__linkcircle {
    width: 3.125rem;
    height: 3.125rem;
  }
}

.nav__icon {
  width: 62.5%;
  height: 62.5%;
}

.nav__icon svg {
  width: 100%;
  height: 100%;
}

.nav__linktext {
  font-family: "Tenor Sans", Helvetica, Arial, sans-serif;
}

@media (min-width: 62rem) {
  .nav__linktext {
    position: absolute;
    left: 50%;
    bottom: -0.625rem;
    font-size: 0.875rem;
    opacity: 0;
    transform: translateX(-50%);
    transition: all 0.4s;
  }
}

.header__content {
  padding: 3rem 0;
}

@media (min-width: 48rem) {
  .header__content {
    padding: 5rem 0;
  }
}

@media (min-width: 75rem) {
  .header__content {
    padding: 8rem 0;
  }
}

.header__title {
  margin-top: 0;
}

@media (min-width: 48rem) {
  .header__title {
    line-height: 3rem;
    font-size: 2.375rem;
  }
}

.header__intro {
  margin: 0 auto;
  max-width: 700px;
  line-height: 1.875rem;
  text-align: left;
  font-size: 1.25rem;
}

@media (min-width: 48rem) {
  .header__intro {
    line-height: 2.1875rem;
    font-size: 1.5625rem;
  }
}

.footer {
  margin-top: 5rem;
  font-size: 0.8rem;
}

@media (min-width: 48rem) {
  .footer {
    margin-top: 7rem;
  }
}

@media (min-width: 62rem) {
  .footer {
    font-size: 0.9rem;
  }
}

.footer__icons {
  margin-bottom: 1rem;
}

.link {
  font-weight: bold;
  transition: color 0.25s;
}

.link--grey {
  color: #5b5c5b;
}

.link--grey:hover {
  color: #8e8f8e;
}

.icon--social {
  display: inline-block;
  width: 30px;
  height: 30px;
}

.icon--social:nth-child(n+2) {
  margin-left: 6px;
}

.icon--social .fill {
  fill: #5b5c5b;
}

.icon--social:hover .fill {
  fill: #8e8f8e;
}

@media (min-width: 48rem) {
  .text {
    line-height: 1.75rem;
    font-size: 1.125rem;
  }
}

@media (min-width: 62rem) {
  .text {
    line-height: 2rem;
    font-size: 1.375rem;
  }
}

.text--light {
  color: #fff;
}

.text__body {
  max-width: 50rem;
  margin: 0 auto;
  text-align: left;
}

.textblock {
  margin: 0 auto;
  max-width: 53.125rem;
  text-align: left;
}

@keyframes rise {
  0% {
    transform: translate3d(-20px, -0px, 0);
  }
  10% {
    transform: translate3d(20px, -50px, 0);
  }
  20% {
    transform: translate3d(-20px, -100px, 0);
  }
  30% {
    transform: translate3d(20px, -150px, 0);
  }
  40% {
    transform: translate3d(-20px, -200px, 0);
  }
  50% {
    transform: translate3d(20px, -250px, 0);
  }
  60% {
    transform: translate3d(-20px, -300px, 0);
  }
  70% {
    transform: translate3d(20px, -350px, 0);
  }
  80% {
    transform: translate3d(-20px, -400px, 0);
  }
  90% {
    transform: translate3d(20px, -450px, 0);
    opacity: 1;
  }
  100% {
    transform: translate3d(-20px, -500px, 0);
    opacity: 0;
  }
}

@keyframes bob {
  50% {
    transform: translateY(-20px);
  }
}

.region .bubble--rise1 {
  animation: rise 15s 0s infinite;
}

.region .bubble--rise2 {
  animation: rise 15s 5s infinite;
}

.region .bubble--rise3 {
  animation: rise 15s 10s infinite;
}

.region .bubble--bob1 {
  animation: bob 3s 0s infinite;
}

.region .bubble--bob2 {
  animation: bob 3s 2s infinite;
}

.region .bubble--bob3 {
  animation: bob 3s 4s infinite;
}

.skillsfeature {
  background: #fff;
  margin: 0 auto;
  max-width: 62.5rem;
}

@media (min-width: 48rem) {
  .skillsfeature {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  }
}

.skillsfeature__skill:first-child .skillsfeature__title {
  box-shadow: none;
}

.skillsfeature__skill--current .skillsfeature__read {
  display: none;
}

@media (min-width: 62rem) {
  .skillsfeature__skill--current .skillsfeature__close {
    display: block;
  }
}

.skillsfeature__skill--current .skillsfeature__overlay {
  opacity: 0;
  transition: opacity 0.25s;
}

.skillsfeature__trigger {
  display: block;
  position: relative;
  height: 5.3125rem;
  background: #e6e6e6;
  line-height: 5.3125rem;
  color: #5b5c5b;
  transition: background 0.25s;
}

.skillsfeature__trigger:hover, .skillsfeature__trigger:focus {
  background: #cccccc;
}

.skillsfeature__trigger:hover .skillsfeature__tooltip {
  opacity: 1;
}

.skillsfeature__title {
  margin: 0;
  box-shadow: 0 -1.875rem 1.5625rem -0.625rem #fff;
}

.skillsfeature__tooltip {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  height: 5.3125rem;
  padding-right: 30px;
  line-height: 5.3125rem;
  opacity: 0;
  transition: opacity 0.25s;
}

@media (max-width: 61.9375rem) {
  .skillsfeature__tooltip {
    display: none;
  }
}

.skillsfeature__close {
  display: none;
}

.skillsfeature__content {
  margin: 0 auto;
  width: 90%;
  height: 6.25rem;
  max-width: 46.875rem;
  overflow: hidden;
  text-align: left;
  transition: height 0.5s;
}

.skillsfeature__contentinner {
  margin: 0 auto;
  padding: 1.875rem 0;
}

.skillsfeature__contentinner p:first-child {
  margin-top: 0;
