.demosfeature {
  margin: 0 auto;
  max-width: 75rem;
}

@media (min-width: 48rem) {
  .demosfeature {
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.25);
