  transition: all 0.25s;
}

.demosfeature__link:hover, .demosfeature__link:focus {
  background: rgba(0, 0, 0, 0.5);
}

.demosfeature__link:hover .demosfeature__tooltip, .demosfeature__link:focus .demosfeature__tooltip {
  opacity: 1;
  transform: translateY(-30px);
}

.demosfeature__text {
  padding: 30px;
}

@media (min-width: 62rem) {
  .demosfeature__text {
    padding: 100px;
  }
}

.demosfeature__title {
  margin-top: 0;
}

.demosfeature__description {
  margin: 0;
  text-align: left;
}

.demosfeature__tooltip {
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  opacity: 0;
  transition: opacity 0.25s, transform 0.4s;
}

@media (max-width: 61.9375rem) {
  .demosfeature__tooltip {
    display: none;
  }
}

