@media (min-width: 48rem) {
  .hidden-md-min {
    display: none;
  }
}

.displayed-md-only {
  display: none;
}

@media (min-width: 48rem) and (max-width: 61.9375rem) {
  .displayed-md-only {
    display: block;
  }
}

@media (min-width: 48rem) {
  .hidden-md-min {
    display: none;
  }
}

.is-displayed-lg {
  display: none;
}

@media (min-width: 62rem) {
  .is-displayed-lg {
    display: block;
  }
}

.is-displayed-md {
  display: none;
}

@media (min-width: 48rem) {
  .is-displayed-md {
    display: block;
  }
}

@media (min-width: 62rem) {
  .is-hidden-lg {
    display: none;
  }
}

@keyframes in-from-left {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes in-from-top {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes in-from-bottom {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes in-from-right {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes in-from-fade {
  100% {
    opacity: 1;
  }
}

@keyframes out-to-left {
  100% {
    transform: translateX(-100%);
  }
}

@keyframes out-to-top {
  100% {
    transform: translateY(-100%);
  }
}

@keyframes out-to-bottom {
  100% {
    transform: translateY(100%);
  }
}

@keyframes out-to-right {
  100% {
    transform: translateX(100%);
  }
}

@keyframes out-to-fade {
  100% {
    opacity: 0;
  }
}

@keyframes button-slide-right {
  50% {
    transform: translate(100%, -50%);
    opacity: 0;
  }
}

@keyframes button-slide-left {
  50% {
    transform: translate(-100%, -50%);
    opacity: 0;
  }
}

@keyframes button-slide-up {
  50% {
    transform: translate(-50%, -100%);
    opacity: 0;
  }
}

@keyframes button-slide-down {
  50% {
    transform: translate(-50%, 100%);
    opacity: 0;
  }
}

.js-outgoing {
  z-index: 8;
}

.js-outgoing-left {
  animation: out-to-left 1s;
}

.js-outgoing-top {
  animation: out-to-top 1s;
}

.js-outgoing-right {
  animation: out-to-right 1s;
}

.js-outgoing-bottom {
  animation: out-to-bottom 1s;
}

.js-outgoing-fade {
  animation: out-to-fade 0.5s forwards;
}

.js-incoming {
  z-index: 9;
}

.js-incoming-left {
  animation: in-from-left 1s;
}

.js-incoming-top {
  animation: in-from-top 1s;
}

.js-incoming-right {
  animation: in-from-right 1s;
}

.js-incoming-bottom {
  animation: in-from-bottom 1s;
}

.js-incoming-fade {
  opacity: 0;
  animation: in-from-fade 0.5s 0.5s;
}

.js-moving-regions .direction {
  pointer-events: none;
}

.js-moving-regions .direction--next {
  animation: button-slide-right 1s;
}

.js-moving-regions .direction--prev {
  animation: button-slide-left 1s;
}

.js-moving-regions .direction--up {
  animation: button-slide-up 1s;
}

.js-moving-regions .direction--down {
  animation: button-slide-down 1s;
}

.js-moving-regions .direction i {
  transition: all 0.2s;
}

.js-moving-regions .direction i:first-of-type {
  transform: rotate(90deg);
}

.js-moving-regions .direction i:last-of-type {
  transform: none;
}

.js-moving-regions .direction__text {
  display: none;
}

.js-moving-regions .direction:hover .direction__icon::after {
  opacity: 1;
}

.js-nav-backgrounds .nav__menu,
.js-nav-backgrounds .direction {
  background-color: rgba(0, 0, 0, 0.5);
}

.direction.js-hidden-button {
  pointer-events: none;
  animation: none;
  opacity: 0;
  transition: all 0.5s;
}

.direction.js-visible-button {
  transform: none;
  opacity: 1;
  transition: all 0.5s 0.5s;
  animation: none;
}

.direction--prev.js-hidden-button {
  transform: translateX(-100%);
}

.direction--next.js-hidden-button {
  transform: translateX(100%);
}

.direction--up.js-hidden-button {
  transform: translateY(-100%);
}

.direction--down.js-hidden-button {
