}

.nav--grey.active .topmenu__trigger {
  color: #ffb732;
}

.nav--grey.active .arrow::after {
  background: #ffb732;
}

.nav--grey.active .arrow::before {
  background: #ffb732;
}

.nav .fill {
  fill: #404040;
}

.nav .stroke {
  stroke: #404040;
}

.nav circle {
  stroke: #404040;
  fill: rgba(255, 255, 255, 0);
}

