}

.nav__link--current .fill, .nav__link:hover .fill, .nav__link:focus .fill {
  fill: #ffae19;
}

.nav__link--current .fill--onlyhover, .nav__link:hover .fill--onlyhover, .nav__link:focus .fill--onlyhover {
  fill: #ffae19;
}

.nav__link--current .stroke, .nav__link:hover .stroke, .nav__link:focus .stroke {
